
@use '../../css/variables' as var;
@use '../../css/mixin' as mix;

.v-works02 {
	padding: 0;
	margin: 0;
	list-style: none;
	display: grid;
	grid-template-columns: 1fr;
	gap: 140px 0;

	@media (var.$break-sp) {
		gap: 52px 0;
	}

	&__title {
		font-size: 1.44rem;
		line-height: calc(17 / 14.4);
		margin: 34px 0 0;

		@media (var.$break-sp) {
			margin-top: 24px;
		}
	}

	&__content {
		p {
			margin: 15px 0 0;
		}
	}
}
