
@use '../../css/variables' as var;
@use '../../css/mixin' as mix;

.v-news {
	padding: 0;
	margin: 0;
	list-style: none;

	&__item {
		border-top: 1px solid var.$color-border;

		&:last-of-type {
			border-bottom: 1px solid var.$color-border;
		}

		> a {
			display: flex;
			align-items: center;
			padding: 17px 14px;
			color: var.$color-font-primary;
			text-decoration: none;

			@media (var.$break-sp) {
				display: block;
			}
		}

		&-time {
			margin-right: 76px;
		}

		&-title {
			flex: 1;
			margin: 0;
		}
	}
}
